.buttons-container{
    font-size: 20px;
    display: flex;
    
    
}
.button{
    background-color: aliceblue;
    margin:20px;
    width:50px;
    height:50px;
    text-align: center;
    line-height:50px;
    border-radius: 30%;
    box-shadow:5px 5px 4px  rgb(0, 140, 255), 10px 10px 4px rgba(222, 50, 196, 0.598), 10px 10px 10px; 
    border-style:solid;
    border-color:rgb(255, 255, 255);
}
.button:hover{

    border-style:solid;
    border-color:rgb(0, 0, 0);
    cursor:pointer;
}